.virtual{
  margin-bottom: 6rem;
  margin-top: 6rem;
  padding: 2rem;
  display: flex;
  justify-content: space-around;
  .right{
    .wrapper{
      width: 30rem;
    }
  }
  .left{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    position: relative;
    img{
      position: absolute;
      width: 10rem;
      bottom: -2rem;

    }
  }
  .left>:nth-child(1){
    font-size: 2rem;
    text-transform: uppercase;
    display: block;
    font-weight: bold;
  }
  .left>:nth-child(2){
    font-size: 1.6rem;
    text-transform: uppercase;
    display: block;
    width: 20rem;
  }
  .left>:nth-child(3){
    font-size: 1.6rem;
    font-weight: bold;

  }


}