 .container{
   padding: 1.5rem ;
   display: flex;
   .logo{
     display: flex;
     align-items: center;
     flex: 1;
     gap: 1rem;
     img{
       width: 2.5rem;
       height: 2.5rem;
     }
     span{
       font-weight: 600;
     }
   }
   .right{
     display: flex;
     gap: 2rem;
     align-items: center;
     justify-content: center;
     .menu{
       display: flex;
       gap: 2rem;
       font-weight: 500;
       list-style: none;
       li{
         &:hover{
           color: #FE956F;
           cursor: pointer;
         }
       }
     }
     .search{
       width: 7rem;
       outline: none;
       border: none;
       height: 30%;
       border-radius: 5px;
       padding: 0.5rem;

     }
     .cart{
       width: 1.5rem;
       height: 1.5rem;
       cursor: pointer;
     }

   }
 }