.container{
  padding: 0 2rem;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
  justify-content: center;
  .container>:nth-child(1){
    position: absolute;
    width: 8rem;
    left: 30%;
    top: -3rem;
  }
  .products{
    display: grid;
    width: 90%;
    grid-template-columns: 25% auto;
    .list{
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
      height: 25rem;
      overflow-y: scroll;
      grid-gap: 2rem;
      justify-content: space-between;

    }
    .list{
      .product{
        width: 12rem;
        height: 8rem;
        background-color: white;
        position: relative;
        overflow: hidden;
        padding: 1rem;
        border-radius: 1rem;
        img{
          top:3rem;
          width: 6rem;
          height: 11rem;

        }
      }
    }
  }
  .menu{
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    font-weight: 500;
    font-size: 1.3rem;
    li{
      &:hover{
        color: var(--pink);
        cursor: pointer;
      }
    }
  }


}